<script>

export default {
  onLaunch() {
    // 展示本地存储能力
    const logs = uni.getStorageSync('logs') || [];
    logs.unshift(Date.now());
    uni.setStorageSync('logs', logs);
    var that = this; // 登录

    uni.login({
      success: res => {
        // 发送 res.code 到后台换取 openId, sessionKey, unionId
        var that = this;

        if (res.code) {
          //发起网络请求
          uni.request({
            url: that.$options.globalData.domain + '/wx/account/login',
            data: {
              code: res.code
            },
            success: request_res => {
              that.$options.globalData.session_3rd = request_res.data;
            }
          }); // console.log("login ok")
        } else {
          console.log('登录失败！' + res.errMsg);
        }
      }
    });
  },

  globalData: {
    checkSession_3rd: function () {
		var that=this
      uni.request({
        url: getApp().globalData.domain+'wx/session/checkSession_3rd',
        data: {
          session_3rd: getApp().globalData.session_3rd
        },
        success: request_res => {
          if (request_res.data == false) {
            uni.showToast({
              title: '会话过期需重载',
              icon: 'error',
              duration: 5000,
              success: function () {
                //弹窗后执行，可以省略
                setTimeout(function () {
                  uni.reLaunch({
                    url: '../index/index'
                  });
                }, 1500);
              }
            });
          }
        }
      });
    },
    userInfo: null,
    domain: "http://localhost:8888",
    socket: "ws://localhost:8888",
    session_3rd: "",
    chats: []
  },
  methods: {}
};
</script>
